.out {
    max-width: 400px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    padding: 20px;
  }
  
  h2 {
    text-align: center;
    color: #333;
  }
  
  .input-group {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .input-group input {
    flex: 1;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px 0 0 4px;
  }
  
  .input-group .btn {
    padding: 10px 20px;
    background-color: #28a745;
    color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 0 4px 4px 0;
  }
  
  .input-group .btn:hover {
    background-color: #218838;
  }
  
  .list-sort {
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    background-color: #f9f9f9;
  }
  
  .list-item {
    display: flex;
    align-items: center;
    padding: 10px;
    margin-bottom: 5px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #fff;
    cursor: move;
  }
  
  .list-item:last-child {
    margin-bottom: 0;
  }
  
  .list-item i {
    margin-right: 10px;
    color: #555;
  }
  
  .list-item h3 {
    margin: 0;
    font-size: 16%;
    color: #333;
  }
  
  .list-item:active {
    background-color: #e9ecef;
  }
  













